.todo-container {
  margin-top: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.save-btn {
  width: 50px;
  height: 30px;
  border-radius: 8px;
  border: none;
  outline: none;
  margin-left: 5px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 400;
  color: black;
  background-color: antiquewhite;
  margin-bottom: 15px;
  vertical-align: middle;
  padding: 4px;
}
.cancel-btn {
  width: 50px;
  height: 30px;
  border-radius: 8px;
  border: none;
  outline: none;
  margin-left: 5px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bolder;
  color: red;
  background-color: antiquewhite;
  margin-bottom: 15px;
  vertical-align: middle;
}
.save-btn:hover {
  color: white;
  background-color: green;
  transition: 0.5s ease-in-out;
}
.cancel-btn:hover {
  color: white;
  background-color: red;
  transition: 0.5s ease-in-out;
}
.text-input {
  width: 250px;
  height: 30px;
  border-radius: 8px;
  border: none;
  outline: none;
  padding-left: 15px;
  font-size: 18px;
  background-color: antiquewhite;
}
.save-texts {
  color: rgb(104, 201, 104);
}
.todo-text::placeholder {
  color: cornflowerblue;
  font-size: 14px;
}
